import { Trel } from '../class/trel.class';
import { createTooltip } from './tooltip.component';

interface TrelConfiguration {
	trel: THREE.Object3D;
	name: string;
	x: number;
	z: number;
	hallCapacity: number;
}

function createCoordinates(
	startPosition: number, 
	spaceBetween: number, 
	hallCapacity: number
) {
	return [...Array(hallCapacity).keys()].reduce((acc, _) => {
		acc.push(startPosition);

		startPosition += spaceBetween;

		return acc;
	}, []);

}

export const createTrel = (
    THREE: any, 
    scene: THREE.Scene
) => ({ 
	trel, 
	name, 
	x, 
	z, 
	hallCapacity
}: TrelConfiguration): void => {
	trel.name = name;
	trel.position.set(x, 0, z);
	trel.rotateY(Math.PI / 2);

	createTooltip(THREE, trel);

	trel.userData = new Trel(trel, createCoordinates(x, 5, hallCapacity).reverse());

	scene.add(trel);
}