function importAll(r: any) {
    return r.keys().map(r);
}
function nameHandler(path: any) {
    const splited = path.default.split("/");
    const id = splited[splited.length - 1];

    return id;
}
const carImages = importAll(require.context('../../static/cars', false, /\.(png)$/));
const countryIcons = importAll(require.context('../../static/flags', false, /\.(ico)$/));

export const CAR_IMAGE = { 
    ...carImages.reduce((acc: any, img: any) => ({ ...acc, [nameHandler(img)]: img.default }), {})
}
export const COUNTRY_ICON = {
    ...countryIcons.reduce((acc: any, img: any) => ({ ...acc, [nameHandler(img)]: img.default }), {})
}