export const readFailedHandler = (scene: THREE.Scene) => (
	topic: string, 
	DATA: any
): void => {
	const pointId = topic.split("_")[0];
	const key = ({ "IP01": "PI01" })[pointId] || pointId;
	const point = scene.getObjectByName(key);

	point.userData.readFailed = JSON.parse(DATA)[pointId];
}