import { CSS2DObject } from "three/examples/jsm/renderers/CSS2DRenderer";

export const createTooltip = (THREE: any, target: any) => {
    const carSlot = new THREE.Group();

	carSlot.name = "CAR_SLOT";

	const tooltipElement = document.createElement('div');

	tooltipElement.style.borderRadius = '50px';
	tooltipElement.style.width = '35px';
	tooltipElement.style.height = '35px';
	tooltipElement.style.color = 'white';
	tooltipElement.style.fontSize = '10px';
	tooltipElement.style.fontWeight = 'bold';
	tooltipElement.style.animationName = '';
	tooltipElement.style.animationDuration = '1.5s';
	tooltipElement.style.animationIterationCount = 'infinite';

	const tooltipObject = new CSS2DObject(tooltipElement);

	tooltipObject.name = 'ALERT_SLOT';
	tooltipObject.visible = false;
	tooltipObject.position.set(0, 0, 0);

	const labelElement = document.createElement('div');

	labelElement.className = 'label';

	const labelObject = new CSS2DObject(labelElement);

	labelObject.name = 'LABEL_SLOT';
	labelObject.visible = false;
	labelObject.position.set(0, 0, 0);

	target.add(labelObject);
	target.add(tooltipObject);
	!target.getObjectByName('CAR_SLOT') ? target.add(carSlot) : null;
}