export const lineCellsSetter = ({
    'Z0': (size: number) => ({
        ...[...Array(size).keys()].reduceRight((acc: any, index: any) => {
            acc.cellId = {
                ...acc.cellId,
                [index]: `CELL_${acc.enumStart}`  
            };

            acc.enumStart += 10;

            return acc;
        },{ 
            enumStart: 1010, 
            cellId: {} 
        }).cellId
    }),
    'Z1': (size: number) => ({
        ...[...Array(size).keys()].reduceRight((acc: any, index: any) => {
            acc.cellId = {
                ...acc.cellId,
                [index]: `CELL_${acc.enumStart}`  
            };

            acc.enumStart += 10;

            return acc;
        },{ 
            enumStart: 1011, 
            cellId: {} 
        }).cellId
    })
    
})