import * as mqtt from "mqtt";

const URL = `ws://${process.env.API}/mqtt`;

const buildConnection = (
    clientName: string, 
    subscribers: string[]
) => {
    const CLIENT = mqtt.connect(URL, { clean: true });

    return new Promise<any>((resolve, reject) => {
        CLIENT.on('connect', () => {
            console.info(`${clientName} Connected !`)

            for (const subscribe of subscribers) {
                CLIENT.subscribe(subscribe);
            }
        });
        CLIENT.on('error', (e: any) => {
            console.error(e);

            CLIENT.end();

            reject(e);
        });
        CLIENT.on('reconnect', () => console.info(`${clientName} Reconecting...`));

        resolve(CLIENT);
    });
}

