
export class Trel {
    //frontend data
    #trelScene: any;
    #coordinates: number[];
    #lastCell: string;
    #lastHall: string;

    //backend data
    #position: number;
    #fullString: string;
    #operation: number;
    #plcStatus: number;
    #command: number;
    #requestJob: boolean;
    #completed: boolean;
    #ackPlc: boolean;
    #knr: string;
    #carScene: any;

    constructor(
        trelScene: any,
        coordinates: number[]
    ) {
        this.#trelScene = trelScene;
        this.#coordinates = coordinates;
    }

    get trelScene() {
        return this.#trelScene;
    }

    get coordinates() {
        return this.#coordinates;
    }

    get position() {
        return this.#position;
    }

    set position(position: number) {
        this.#position = position;

        this.trelScene.position.x = (
            this.coordinates?.[this.position] || this.trelScene.position.x
        );
    }

    get fullString() {
        return this.#fullString;
    }

    set fullString(fullString: string) {
        this.#fullString = fullString;
    }

    get operation() {
        return this.#operation;
    }

    set operation(operation: number) {
        this.#operation = operation;
    }

    get plcStatus() {
        return this.#plcStatus;
    }

    set plcStatus(plcStatus: number) {
        if (this.#plcStatus === plcStatus) return;

        this.#plcStatus = plcStatus;
        
        this.showStatus();
    }

    get command() {
        return this.#command;
    }

    set command(command: number) {
        if (this.#command === command) return;

        this.#command = command;

        this.showStatus();
    }

    get requestJob() {
        return this.#requestJob;
    }

    set requestJob(requestJob: boolean) {
        this.#requestJob = requestJob;
    }

    get completed() {
        return this.#completed;
    }

    set completed(completed: boolean) {
        this.#completed = completed;
    }

    get ackPlc() {
        return this.#ackPlc;
    }

    set ackPlc(ackPlc: boolean) {
        this.#ackPlc = ackPlc;
    }

    get knr() {
        return this.#knr;
    }

    set knr(knr: string) {
        if (this.#knr === knr) return;

        this.#knr = knr;

        this.showStatus();
    }

    get carScene() {
        return this.#carScene;
    }

    set carScene(carScene: any) {
        this.#carScene = carScene;
    }

    get lastCell() {
        return this.#lastCell;
    }

    set lastCell(lastCell: string) {
        this.#lastCell = lastCell;
    }

    get lastHall() {
        return this.#lastHall;
    }

    set lastHall(lastHall: string) {
        this.#lastHall = lastHall;
    }

    private showStatus() {
        const animationType = ({ 
            0: 'automatic',
            1: 'fail',
            2: 'manual',
            5: 'blue-whale'
        })[this.plcStatus];

        const labelSlot = this.trelScene.getObjectByName('ALERT_SLOT');

        labelSlot.element.style.animationName = animationType;
        labelSlot.visible = (animationType !== 'automatic');
    }

    public setProps(DATA: any) {
        this.plcStatus = DATA?.STATUS_PLC;
        this.position = DATA?.POS;
        this.fullString = DATA?.FULL_STRING;
        this.operation = DATA?.COMANDO;
        this.command = DATA?.COMANDO;
        this.requestJob = DATA?.REQUEST_JOB;
        this.completed = DATA?.COMPLETED;
        this.ackPlc = DATA?.ACKPLC;
        this.knr = DATA?.CAR?.KNR;
    }

    

}