import { createTooltip } from './tooltip.component';

interface Coordinate {
    x?: number;
    y?: number;
    z?: number;
}
interface TableStyle {
    customId?: string
    rotation: Coordinate;
    position: Coordinate;
    color?: any;
    opacity?: number;
    spaceBetweenTable?: number;
}
interface Line {
    id: string;
    capacity: number;
    trel?: string;
    position: Coordinate;
    rotation: Coordinate;
    invert?: boolean;
    tableStyle: TableStyle;
}

export const createLine = (
    THREE: any, 
    scene: THREE.Scene
) => ({
	id, 
	capacity,
	trel = null, 
	position,
	rotation,
	invert,
	tableStyle
}: Line): void => {
	const group = new THREE.Group();

	group.name = id;
	group.userData.trel = trel;
	group.rotation.set(rotation.x, (rotation.y || Math.PI / 2), rotation.z);
	group.position.set(position.x, position.y, position.z);

	for (let i = 0; i < capacity; i++) {
		const table = new THREE.Mesh( 
			new THREE.BoxGeometry( 3.0, 0.5, 4 ), 
			new THREE.MeshToonMaterial( { 
				color: tableStyle[i].color || 0xB3AFAF,
				opacity: tableStyle[i].opacity || 0.8,
				transparent: true 
			} )
		);
		
		table.name = tableStyle[i]?.id || `CELL_${i}`;

		table.rotation.set(
			tableStyle[i].rotation.x, 
			tableStyle[i].rotation.y, 
			tableStyle[i].rotation.z
		);
		table.position.set(
			tableStyle[i].position.x, 
			tableStyle[i].position.y , 
			i * tableStyle[i].spaceBetweenTable
		);

		createTooltip(THREE, table);

		group.add(table);
	}

	if (invert) group.children.reverse();

	scene.add(group);
}