import { COUNTRY_NAME } from "../helper/countrydescription.helper";
import { CAR_IMAGE, COUNTRY_ICON } from "../helper/image.helper";
import { introduction1IdSetter, introduction2IdSetter, PointIIdSetter, K110IdSetter, MSHallIdSetter } from "../helper/warehouseIdRelation.helper";

export const carInformationEvent = (
    THREE: any, 
    scene: THREE.Scene,
    camera: THREE.PerspectiveCamera
) => {
    const raycaster = new THREE.Raycaster();
    const mouse = new THREE.Vector2();
    const targets = [
		...Object.values(introduction1IdSetter),
		...Object.values(introduction2IdSetter),
		...Object.values(PointIIdSetter),
		...Object.values(K110IdSetter),
		...Object.values(MSHallIdSetter),
		'PI01', 'ME01', 'ME02', 'ME03', 'ME04',
		'PK01', 'MS01', 'MS02', 'MS03', 'MS04',
		'VW312', 'CELL','TREL'
	];

    return (event: any) => {
        event.preventDefault();
            
        mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
        
        raycaster.setFromCamera(mouse, camera);
        
        const intersects = raycaster.intersectObjects(scene.children, true);

        const cell = intersects
            .find(({ object }) => 
                targets.some((target) => object.name.startsWith(target))
            );

        if (cell?.object?.getObjectByName('CAR')) {

            const labelSlot = cell.object.getObjectByName('LABEL_SLOT') as any;

            labelSlot.visible = true;
            labelSlot.element.innerHTML = '';
            labelSlot.element.style.textAlign = 'center';

            const carData = cell.object.getObjectByName('CAR').userData;

            const carImg = document.createElement("img");

            carImg.width = 150;
            carImg.height = 150;
            carImg.style.width = '100%';
            carImg.style.maxWidth = '215px';
            carImg.style.height = 'auto';
            carImg.src = CAR_IMAGE[`${carData.model}_${carData.farbau}.png`];

            const countryImg = document.createElement("img");

            countryImg.width = 35;
            countryImg.height = 35;
            countryImg.src = COUNTRY_ICON[`${carData.vzland}.ico`];
            countryImg.title = COUNTRY_NAME[carData.vzland];

            const div = document.createElement("div");
            const p = document.createElement("p");

            div.style.display = 'flex';
            p.style.fontWeight = 'bold';
            p.innerText = `${carData.knr} - ${carData.model} - ${carData.farbau}`;

            div.appendChild(p);
            div.appendChild(countryImg);

            labelSlot.element.appendChild(carImg);
            labelSlot.element.appendChild(div);

            setTimeout(() => {
                cell.object.getObjectByName('LABEL_SLOT').visible = false;
            }, 5000);
        }
    }
}