import { carType } from "../helper/cartype.helper";
import { COLOR } from "../helper/colors.helper";
import { PART_COLOR } from "../helper/carpartcolor.helper";

export class Car {
    //frontend data
    uuid: any;
	carType: string;

    //backend data
    werk: string;
    spj: string;
    knr: string;
    knra: string;
    farbau: string;
    vzland: string;
    model: string;
    angca: string;
    line: number;
    cell: number;
    
    constructor(
        car: any,
        uuid: any,
        WERK: string,
        SPJ: string,
        KNR: string,
        KNRA: string,
        FARBAU: string,
        VZLAND: string,
        MODELL: string,
        FANLAGE: string,
        Z: number,
        Y: number,
    ) {
        this.uuid = uuid;
        this.werk = WERK;
        this.spj = SPJ;
        this.knr = KNR;
        this.knra = KNRA;
        this.farbau = FARBAU;
        this.vzland = VZLAND;
        this.model = MODELL;
        this.angca = FANLAGE;
        this.line = Z;
        this.cell = Y;

        this.carType = carType(this.model);

		const color = COLOR[this.farbau];

        if (!color) {
            console.log(`[COLOR][NOT_FOUND]: ${this.farbau}`);
            return;
        }

        if (!PART_COLOR[this.carType]) {
            console.log(`[PART_COLOR][NOT_FOUND]: ${this.model}`);
            return;
        }

        const parts = PART_COLOR[this.carType];

        const [firstColor, secondColor] = color instanceof Object ? (() => {
            const [first, second] = [this.farbau.substring(0, 2), this.farbau.substring(2, 4)];

            return [color[first], color[second]];
        })() : [color, null];

        for (const part of parts) {
            if (part instanceof Array) {
                const [fatherPart, childParts] = part;

                const fatherVehiclePart = car.getObjectByName(fatherPart);

                for (const childPart of childParts) {
                    if (childPart instanceof Object) {
                        const { name, secondColorPaint } = childPart;

                        const childVehiclePart = fatherVehiclePart.getObjectByName(name);

                        if (!childVehiclePart?.material) continue;

                        const materialClone = childVehiclePart.material.clone();

                        materialClone.color.setHex(
                            secondColorPaint && 
                            secondColor !== null ? 
                            secondColor : firstColor
                        );

                        childVehiclePart.material = materialClone;
                    } else {
                        const childVehiclePart = fatherVehiclePart.getObjectByName(childPart);

                        if (!childVehiclePart?.material) continue;

                        const materialClone = childVehiclePart.material.clone();

                        materialClone.color.setHex(firstColor);

                        childVehiclePart.material = materialClone;
                    }
                }
            } else {
                if (part instanceof Object) {
                    const { name, secondColorPaint } = part;

                    const vehiclePart = car.getObjectByName(name);

                    if (!vehiclePart?.material) continue;

                    const materialClone = vehiclePart.material.clone();

                    materialClone.color.setHex(
                        secondColorPaint && 
                        secondColor !== null ? 
                        secondColor : firstColor
                    );

                    vehiclePart.material = materialClone;
                } else {
                    const vehiclePart = car.getObjectByName(part);

                    if (!vehiclePart?.material) continue;
                    
                    const materialClone = vehiclePart.material.clone();
    
                    materialClone.color.setHex(firstColor);
        
                    vehiclePart.material = materialClone;
                }

               
            }
        }

    }
}