import { Point } from "../class/point.class";
import { createTooltip } from "./tooltip.component";

interface PointConfiguration {
    id: string;
    x: number;
    z: number;
    rotation?: number;
}

export const createPoint = (
    THREE: any, 
    scene: THREE.Scene
) => ({
	id, 
	x, 
	z, 
	rotation = Math.PI / 2
}: PointConfiguration): void => {
	const pointTable = new THREE.Mesh( 
		new THREE.CylinderGeometry(2.5, 2.5, 0.5, 32), 
		new THREE.MeshToonMaterial({ 
			color: 0xB3AFAF,
			side: THREE.DoubleSide,
			transparent: true,
			opacity: 0.8 
		})
	);

	pointTable.name = id;
	pointTable.position.set(x, 0, z);
	pointTable.rotation.y = rotation;

    createTooltip(THREE, pointTable);

	pointTable.userData = new Point(pointTable);

	scene.add(pointTable);
}
