import { trelHandler } from "./trel.handler";
import { ancgaHandler } from './ancga.handler';
import { pointHandler } from './point.handler';
import { readFailedHandler } from './readFailed.handler';
import { bufferHandler } from './buffer.handler';
import { rkaaHandler } from './rkaa.handler';
import { outHandler } from './out.handler';
import { inHandler } from './in.handler';

module.exports = (scene: THREE.Scene) => ({ 
    trelHandler: trelHandler(scene),
    ancgaHandler: ancgaHandler(scene),
    pointHandler: pointHandler(scene),
    readFailedHandler: readFailedHandler(scene),
    bufferHandler: bufferHandler(scene),
    rkaaHandler: rkaaHandler(scene),
    outHandler: outHandler(scene),
    inHandler: inHandler(scene),
});
