function importAll(r: any) {
    return r.keys().map(r);
}
function nameHandler(path: any) {
    const splited = path.default.split("/");
    const id = splited[splited.length - 1];

    return id;
}

const carModels = importAll(require.context('../../static/model', false, /\.(glb)$/));

export const CAR_MODEL = { ...carModels.reduce((acc: any, model: any) => ({ ...acc, [nameHandler(model)]: model.default }), {}) }