export class Point {
    //frontend data
    #pointScene: any;

    //backend data
    #readFailed: boolean;
    #operation: number;
    #statusPlc: number;
    #requestJob: boolean;
    #knr: string;
    #carScene: any;
    
    constructor(pointScene: any) {
        this.#pointScene = pointScene;
    }

    get pointScene() {
        return this.#pointScene;
    }
    
    get requestJob() {
        return this.#requestJob;
    }

    get operation() {
        return this.#operation;
    }

    get knr() {
        return this.#knr;
    }

    get statusPlc() {
        return this.#statusPlc;
    }

    get readFailed() {
        return this.#readFailed;
    }

    set knr(knr: string) {
        if (knr === this.knr) return;

        this.#knr = knr;

        this.showStatus();
    }

    set requestJob(requestJob: boolean) {
        this.#requestJob = requestJob;
    }

    set readFailed(readFailed: boolean) {
        if (readFailed === this.#readFailed) return;

        this.#readFailed = readFailed;

        this.showStatus();
    }

    set operation(operation: number) {
        this.#operation = operation;
    }

    set statusPlc(statusPlc: number) {
        if (statusPlc === this.#statusPlc) return;

        this.#statusPlc = statusPlc;

        this.showStatus();
    }

    get carScene() {
        return this.#carScene;
    }

    set carScene(carScene: any) {
        this.#carScene = carScene;
    }

    showStatus() {
        const animationType = (
            this.statusPlc === 3 ? 'manual' :
            this.statusPlc === 2 ? 'fail' :
            !!(this.readFailed === false && this.knr) ? 'read-fail' :
            'automatic'
        );
        const labelSlot = this.pointScene.getObjectByName('ALERT_SLOT');

        labelSlot.element.style.animationName = animationType;
        labelSlot.visible = animationType !== 'automatic';
    }

}