import { carType } from "../helper/cartype.helper";
import { Car } from "./car.class";

export interface Buffer {
    [key: string]: {
        name: string,
        ranges: { from: number, to: number, color: string}[];
    }
}
export interface Extraction {
    [key: string]: {
        name: string,
        knr?: string,
        seq?: string
    }
}

export class WarehouseController {
    #html: any;
    #scene: any;
    #buffer: Buffer;
    #extraction: Extraction;
    #models: any;
    #carsCache: any = {}; //KEY = KNR
    #statusInPoints: string[];
    #statusOutPoints: string[];
    #tableInCells: string[][];
    #tableOutCells: string[][];
    #attributeConvert = { "PI01": "IP01" };

    constructor(
        html: Document, 
        scene: any,
        buffer: Buffer,
        extraction: Extraction,
        models: any,
        {
            statusInPoints,
            statusOutPoints,
            tableInCells,
            tableOutCells
        }: any
    ) {
        this.#html = html;
        this.#scene = scene;
        this.#buffer = buffer;
        this.#extraction = extraction;
        this.#models = models;
        this.#statusInPoints = statusInPoints;
        this.#statusOutPoints = statusOutPoints;
        this.#tableInCells = tableInCells;
        this.#tableOutCells = tableOutCells;
        
        this.createExtractionGUI();
        this.createProgressGUI();
    }

    get statusInPoints() {
        return this.#statusInPoints;
    }

    set statusInPoints(statusIn: any) {
        if (!this.statusInPoints.length) return;

        const mappedPoints = statusIn.reduce((acc: any, curr: any) => ({ ...acc, ...curr }), {});
        
        for (const pointId of this.statusInPoints) {
            const pointTarget = this.scene.getObjectByName(pointId);
            const key = this.#attributeConvert[pointId] || pointId;

            pointTarget.userData.operation = mappedPoints[`${key}_OPERACAO`];  
            pointTarget.userData.statusPlc = mappedPoints[`${key}_STATUS_PLC`];
            pointTarget.userData.requestJob = mappedPoints[`${key}_REQUEST_JOB`];
        }

    }

    get statusOutPoints() {
        return this.#statusOutPoints;
    }

    set statusOutPoints(statusOut: any) {
        if (!this.statusOutPoints.length) return;

        const mappedPoints = statusOut.reduce((acc: any, curr: any) => ({ ...acc, ...curr }), {});
        
        for (const pointId of this.statusOutPoints) {
            const pointTarget = this.scene.getObjectByName(pointId);
            const key = this.#attributeConvert[pointId] || pointId;

            pointTarget.userData.operation = mappedPoints[`${key}_OPERACAO`];  
            pointTarget.userData.statusPlc = mappedPoints[`${key}_STATUS_PLC`];
            pointTarget.userData.requestJob = mappedPoints[`${key}_REQUEST_JOB`];
        }
    }

    get tableInCells() {
        return this.#tableInCells;
    }

    set tableInCells(tableIn: any) {
        if (!this.tableInCells.length) return;

        const mappedTables = tableIn.reduce((acc: any, curr: any) => ({ ...acc, ...curr }), {});

        if (
            mappedTables['VW312'] && 
            mappedTables['VW312_POS']
        ) {
           this.trelPointIHandler(mappedTables);
        }

        for (const table of this.tableInCells) {
            const [hall, cells] = table;

            const hallTarget = this.scene.getObjectByName(hall);

            for (const cellId of cells) {
                const cell = hallTarget.getObjectByName(cellId);
                const key = this.#attributeConvert[cellId] || cellId;
                const DATA = mappedTables[key];
          
                this.switch(DATA, cell);
            }
        }
    }

    get tableOutCells() {
        return this.#tableOutCells;
    }

    set tableOutCells(tableOut: any) {
        if (!this.tableOutCells.length) return;

        const mappedTables = tableOut.reduce((acc: any, curr: any) => ({ ...acc, ...curr }), {});

        for (const table of this.tableOutCells) {
            const [hall, cells] = table;

            const hallTarget = this.scene.getObjectByName(hall);

            for (const cellId of cells) {
                const cell = hallTarget.getObjectByName(cellId);
                const key = this.#attributeConvert[cellId] || cellId;
                const DATA = mappedTables[key];
 
                this.switch(DATA, cell);
            }
        }
    }

    get html() {
        return this.#html;
    }

    get scene() {
        return this.#scene;
    }

    get buffer() {
        return this.#buffer;
    }

    get extraction() {
        return this.#extraction;
    }

    set extraction(extraction: Extraction) {
        this.#extraction = extraction;
    } 

    get carsCache() {
        return this.#carsCache;
    }

    private createExtractionGUI() {
        const container = this.html.createElement("div");
    
        container.className = 'container';
    
        for (const [extractionId, { name }] of Object.entries(this.extraction)) {
            const item = document.createElement("div");
            const header = document.createElement("h4");
            const body = document.createElement("p");
    
            item.className = 'item';
            header.className = 'item-header';
            body.className = 'item-body';
    
            item.id = extractionId;
    
            header.innerText = name;
    
            item.appendChild(header);
            item.appendChild(body);
    
            container.appendChild(item);
        }
        
        this.html.body.appendChild(container);
    }

    private createProgressGUI() {
        const container = this.html.createElement("div");

        container.className = 'progress-container';

        for (const [bufferId, { name: bufferName }] of Object.entries(this.buffer)) {
            const item = this.html.createElement("div");
            const progress = this.html.createElement("div");
            const bar = this.html.createElement("div");
            const p = this.html.createElement("p");
            const title = this.html.createElement("p");

            item.style.width = '110px';
            item.id = bufferId;

            title.innerText = bufferName;
            title.style.top = 0;
            title.style.color = 'white';
            title.style.fontWeight = 'bold';

            progress.className = 'progress';

            bar.className = 'bar';
            bar.style.height = '0%';

            p.innerText = '0 %';
            
            item.appendChild(title);

            progress.appendChild(p);
            progress.appendChild(bar);

            item.appendChild(progress);

            container.appendChild(item);   
        }

        this.html.body.appendChild(container);
    }

    public updateExtractionGUI(id: string, { knr, seq }) {
        this.extraction[id] = {
            ...this.extraction[id],
            knr, 
            seq
        };
        const extractionDescription = this.html.getElementById(id).querySelector("p");
        
        extractionDescription.innerHTML = (
            !this.extraction[id].knr ? 
            '' : 
            `${this.extraction[id].knr}<br/>SEQ: ${this.extraction[id].seq}`
        );
    }

    public updateProgressGUI(id: string, count: number) {
        const buffer = this.buffer[id];
        const item = this.html.getElementById(id);
        const progress = item.querySelector(".progress").querySelector("p");
        const bar = item.querySelector(".bar");
        const range = buffer.ranges.find((range: any) => +count >= range.from && +count <= range.to);

        bar.style.backgroundColor = range.color;
        bar.style.height = `${count}%`;
        progress.innerText = `${count} %`;
    }

    public createCar(DATA: any) {
        const type = carType(DATA?.MODELL);

        if(!this.#models[type]) return null;
        
        const car = this.#models[type].clone();

        car.userData = new Car(
            car,
            car.uuid, 
            DATA?.WERK,
            DATA?.SPJ,
            DATA?.KNR,
            DATA?.KANR0,
            DATA?.FARBAU,
            DATA?.VZLAND,
            DATA?.MODELL,
            DATA?.FANLAGE0,
            DATA?.Z,
            DATA?.Y
        );
        car.rotation.y = Math.PI / 1;

        return car;
    }

    public switch(DATA: any, target: any) {
        const { KNR } = DATA || {};

        if (KNR) {
            if (target.getObjectByName('CAR')) return;

            const car = this.carsCache[KNR] || this.createCar(DATA);
            const slot = target.getObjectByName('CAR_SLOT');

            slot.add(car);

            this.carsCache[KNR] = car;
        } else if (target.getObjectByName('CAR')) {
            target.getObjectByName('CAR_SLOT').clear();
        }
    }

    public trelPointIHandler(mappedTables: any) {
        const trelPointI = this.scene.getObjectByName('VW312');
        const TREL = mappedTables['VW312'];

        trelPointI.userData.setProps(TREL);

        trelPointI.position.z = mappedTables['VW312_POS'] !== 3 ? -56 : -40;

        this.switch(TREL, trelPointI);
    }

}