import { lines } from "./lines.render";
import { points } from "./point.render";
import { trels } from "./trel.render";

module.exports = (
    THREE: any, 
    scene: THREE.Scene
) => {
    const { 
        createLine, 
        createTrel,
        createPoint
    } = require('../component/module.component')(THREE, scene);

    return ({ 
        lines: () => lines(createLine),
        points: () => points(createPoint),
        trels: (trel: THREE.Object3D) => trels(createTrel, trel)
    })
};
