export const carType = (model: string) => {
    if (model.startsWith('BZ')) {
        const range = +model.replace(/\D/g, "");

        return (
            range >= 20 && range <= 30 || 
            range >= 40 && range <= 50 ? 
            'VIRTUS' : 
            'POLO'
        );
    } else if (model.startsWith('CH')) {
        return 'NIVUS';
    } else if (model.startsWith('5U')) {
        return  (
            ['5UFT','5UFN'].includes(model) ? 
            'SAVEIRO' : 
            ['5UDN','5UDT','5UDW'].includes(model) ?
            'SAVEIRO_CD' :
            'SAVEIRO_CROSS'
        );
    }
    
    return 'GENERIC';
}