import { trelObjectConversion } from '../helper/trelobjectconversion.helper';

export const trelHandler = (scene: THREE.Scene) => (
    topic: string, 
    TREL: any
) => {
    topic = topic.replace('Status', '');
    
    const trel = scene.getObjectByName(topic);

    TREL = trelObjectConversion(JSON.parse(TREL));

    trel.userData.setProps(TREL);
    
    const cellPosition = trel.userData.fullString?.split('*')[5]?.substring(2, 6)?.trim();

    if (+cellPosition) {
        const cellId = `CELL_${+cellPosition.replace(/\D/g, "")}`;
        const hallSide = `Z${cellPosition[cellPosition.length - 1]}`;
        const hallId = `${TREL.CAR.FANLAGE0}_${hallSide}`;
        const cell = scene.getObjectByName(hallId).getObjectByName(cellId) as any;

        trel.userData.lastCell = cellId;
        trel.userData.lastHall = hallId;

        if (trel.userData.operation) {
            cell.material.color.setHex(({
                1: 0x76E072, //introduction
                2: 0x68D7F2, // reintroduction
                3: 0x68D7F2 //extraction
            })[trel.userData.operation]);
        } else {
            cell.material.color.setHex(0xB3AFAF);
        }
    } else if (
        trel.userData.lastCell && 
        trel.userData.lastHall
    ) {
        const cell = scene.getObjectByName(trel.userData.lastHall).getObjectByName(trel.userData.lastCell) as any;

        cell.material.color.setHex(0xB3AFAF) ;

        trel.userData.lastCell = null;
        trel.userData.lastHall = null;
    }

    scene.userData.switch(TREL?.CAR, trel);
}
