export const ancgaHandler = (scene: THREE.Scene) => (
	topic: string, 
	ANCG: any
): void => {
	const ancgMapped = JSON.parse(ANCG.toString()).reduce((acc, curr) => ({
		...acc,
		[`${topic}_Z${curr.Z}`]: [
			...acc[`${topic}_Z${curr.Z}`],
			curr
		]
	}), { [`${topic}_Z0`]: [], [`${topic}_Z1`]: [] });

	for (let [ancg, ancgValue] of Object.entries(ancgMapped) as any) {
		ancgValue = ancgValue.reverse();

		const cells = scene.getObjectByName(ancg).children;

		for (let i = 0; i < cells.length; i++) {
			if (cells[i].getObjectByName('CAR')) continue;

			scene.userData.switch(ancgValue[i], cells[i]);
		}
	}
}