export const createGround = (
    THREE: any, 
    scene: THREE.Scene
) => (): void => {
	const ground = new THREE.Mesh(
		new THREE.PlaneGeometry( 1000, 1000 ), 
		new THREE.MeshPhongMaterial({ 
			color: 0xD6D6D6, 
			depthWrite: false 
		}) 
	);

	ground.rotation.x = - Math.PI / 2;

	scene.add(ground);
}
