import * as THREE from 'three';
import Stats from 'three/examples/jsm/libs/stats.module';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { 
	CSS2DRenderer,
} from 'three/examples/jsm/renderers/CSS2DRenderer.js';
import { WarehouseController } from './class/warehouseController.class'
import { 
	K110IdSetter, 
	PointIIdSetter, 
	introduction1IdSetter, 
	introduction2IdSetter, 
	MSHallIdSetter 
} from './helper/warehouseIdRelation.helper';
import './style.css';
import { CAR_MODEL } from './helper/carloader.helper';
import { OBJECT } from './helper/objectloader.helper';

const camera = new THREE.PerspectiveCamera(
	30,
	window.innerWidth / window.innerHeight,
	0.1,
	1000
);
const renderer = new THREE.WebGLRenderer({ antialias: true });
const labelRenderer = new CSS2DRenderer();
const scene = new THREE.Scene();
const stats = Stats();
const {
	carInformationEvent
} = require('./event/module.event')(THREE, scene, camera);
const {
	grid, 
	createGround
} = require('./component/module.component')(THREE, scene);
const {
	lines,
	points,
	trels
} = require('./render/module.render')(THREE, scene);
const {
	ancgcMqtt,
	trelMqtt,
	bufferMqtt,
	pReadFailedMqtt,
	rkaaMqtt,
	outMqtt,
	inMqtt,
	pMqtt,
	msReadFailedMqtt,
	meReadFailedMqtt,
	meMqtt,
	msMqtt
} = require('./service/module.mqtt');
const { 
	trelHandler, 
	ancgaHandler, 
	pointHandler,
	readFailedHandler,
	bufferHandler,
	rkaaHandler,
	outHandler,
	inHandler
} = require('./handler/module.handler')(scene);

async function loadModels() {
	return Promise.all([
		Promise.all([
			CAR_MODEL['polo-body.glb'],
			CAR_MODEL['nivus-body.glb'],
			CAR_MODEL['virtus-body.glb'],
			CAR_MODEL['saveiro-body.glb'],
			CAR_MODEL['saveiro-cross-body.glb'],
			CAR_MODEL['saveiro-cd-body.glb'],
			CAR_MODEL['generic.glb'],
		].map((href) => 
			new Promise((resolve) => {
				new GLTFLoader()
					.load(href, (car) => {
						const carClone = car.scene.clone();

						carClone.name = 'CAR';

						resolve(carClone);
					});
				})
			)
		),
		new Promise((resolve) => {
			new THREE
				.ObjectLoader()
				.parse(OBJECT['trel.json'], (trel) => resolve(trel));
		})
		
	]);
}

function render() {
	stats.update();

	renderer.render(scene, camera);
	labelRenderer.render(scene, camera);

	requestAnimationFrame(render);
}

async function warehouse() {
	const [models, trel] = await loadModels() as any;

	points();
	lines();
	trels(trel);

	scene.userData = new WarehouseController(document, scene, {
		'KCount': {
			name: 'K110',
			ranges: [
				{ from: 0, to: 10, color: 'rgb(165,13,13)' },
				{ from: 11, to: 24, color: 'rgb(23, 148,12)' }
			]
		},
		'optimoCount': {
			name: 'Optimo',
			ranges: [
				{ from: 0, to: 47, color: '#a50d0d' },
				{ from: 48, to: 59, color: '#92a10c' },
				{ from: 60, to: 100, color: '#17940c' },
			]
		}
	}, {
		'RKAA_01': { name: 'Extrao Corredor 1' },
		'RKAA_02': { name: 'Extrao Corredor 2' },
		'RKAA_03': { name: 'Extrao Corredor 3' },
		'RKAA_04': { name: 'Extrao Corredor 4' }
	}, {
		'POLO': models[0],
		'NIVUS': models[1],
		'VIRTUS': models[2],
		'SAVEIRO': models[3],
		'SAVEIRO_CROSS': models[4],
		'SAVEIRO_CD': models[5],
		'GENERIC': models[6]
	}, {
		statusInPoints: ['PI01', 'ME01', 'ME02', 'ME03', 'ME04'],
		statusOutPoints: ['PK01', 'MS01', 'MS02', 'MS03', 'MS04'],
		tableInCells: [
			['INTRODUCTION_HALL_1', [...Object.values(introduction1IdSetter)]],
			['INTRODUCTION_HALL_2', [...Object.values(introduction2IdSetter)]],
			['POINT_I_HALL', [...Object.values(PointIIdSetter)]]
		],
		tableOutCells: [
			['K110', [ ...Object.values(K110IdSetter)]], 
			['MS_HALL', [...Object.values(MSHallIdSetter)]]
		]
	});

	(await ancgcMqtt).on('message', ancgaHandler);
	(await trelMqtt).on('message', trelHandler);
	(await bufferMqtt).on('message', bufferHandler);
	(await rkaaMqtt).on('message', rkaaHandler);
	(await outMqtt).on('message', outHandler);
	(await inMqtt).on('message', inHandler);
	(await pMqtt).on('message', pointHandler);
	(await meMqtt).on('message', pointHandler);
	(await msMqtt).on('message', pointHandler);
	(await pReadFailedMqtt).on('message', readFailedHandler);
	(await msReadFailedMqtt).on('message', readFailedHandler);
	(await meReadFailedMqtt).on('message', readFailedHandler);

	try {
		setTimeout(() => fetch(`http://${process.env.API}/counters`), 3000);
	} catch (e) {
		console.error(e);
	}
}

function init() {
	renderer.setSize(window.innerWidth, window.innerHeight);
	labelRenderer.setSize(window.innerWidth, window.innerHeight);

	labelRenderer.domElement.style.position = 'absolute';
	labelRenderer.domElement.style.top = '0px';

	camera.position.set(120, 50, -160);
	camera.layers.enableAll();

	const orbit = new OrbitControls(camera, labelRenderer.domElement);

	orbit.minPolarAngle = 0;
	orbit.maxPolarAngle = Math.PI * 0.49;
	orbit.dampingFactor = 0.25;

	orbit.update();

	const hemiLight = new THREE.HemisphereLight(0xFFFFFF, 0x444444);

	hemiLight.position.set(0, 20, 0);

	scene.add(hemiLight);

	createGround();
	grid(150, 25);

	scene.background = new THREE.Color(0xA0A0A0);
	scene.fog = new THREE.Fog(0xA0A0A0, 10, 1000);

    document.body.appendChild(stats.dom);

	renderer.setClearColor(0xB3AFAF);

	window.addEventListener('resize', () => {
		camera.aspect = window.innerWidth / window.innerHeight;

		camera.updateProjectionMatrix();

		labelRenderer.setSize(window.innerWidth, window.innerHeight);
		renderer.setSize(window.innerWidth, window.innerHeight);
	});

	warehouse();

	labelRenderer.domElement.addEventListener('click', carInformationEvent);
	labelRenderer.domElement.addEventListener('touchstart', carInformationEvent);

	document.body.appendChild(renderer.domElement);
	document.body.appendChild(labelRenderer.domElement);

	requestAnimationFrame(render);
}

init();
